import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import PrinterShared_32x32_4 from '../../png/PrinterShared_32x32_4.png';
import PrinterShared_16x16_4 from '../../png/PrinterShared_16x16_4.png';

export const printerSharedData = {
  '32x32_4': {
    imageSrc: PrinterShared_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: PrinterShared_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface PrinterSharedProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const PrinterShared: React.FC<PrinterSharedProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = printerSharedData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
