import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Mailnews8_32x32_4 from '../../png/Mailnews8_32x32_4.png';
import Mailnews8_16x16_4 from '../../png/Mailnews8_16x16_4.png';

export const mailnews8Data = {
  '32x32_4': {
    imageSrc: Mailnews8_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Mailnews8_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Mailnews8Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Mailnews8: React.FC<Mailnews8Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = mailnews8Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
