import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Wintrust103_32x32_4 from '../../png/Wintrust103_32x32_4.png';

export const wintrust103Data = {
  '32x32_4': {
    imageSrc: Wintrust103_32x32_4 as string,
    width: 32,
    height: 32,
  },
};

export interface Wintrust103Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4';
}

export const Wintrust103: React.FC<Wintrust103Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = wintrust103Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
