import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Folder_32x32_4 from '../../png/Folder_32x32_4.png';
import Folder_16x16_4 from '../../png/Folder_16x16_4.png';

export const folderData = {
  '32x32_4': {
    imageSrc: Folder_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Folder_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface FolderProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Folder: React.FC<FolderProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = folderData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
