import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Actmovie303_32x32_4 from '../../png/Actmovie303_32x32_4.png';
import Actmovie303_16x16_4 from '../../png/Actmovie303_16x16_4.png';

export const actmovie303Data = {
  '32x32_4': {
    imageSrc: Actmovie303_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Actmovie303_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Actmovie303Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Actmovie303: React.FC<Actmovie303Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = actmovie303Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
