import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import PrinterDrive_32x32_4 from '../../png/PrinterDrive_32x32_4.png';
import PrinterDrive_16x16_4 from '../../png/PrinterDrive_16x16_4.png';

export const printerDriveData = {
  '32x32_4': {
    imageSrc: PrinterDrive_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: PrinterDrive_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface PrinterDriveProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const PrinterDrive: React.FC<PrinterDriveProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = printerDriveData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
