import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import PowerOff_32x32_4 from '../../png/PowerOff_32x32_4.png';
import PowerOff_16x16_4 from '../../png/PowerOff_16x16_4.png';

export const powerOffData = {
  '32x32_4': {
    imageSrc: PowerOff_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: PowerOff_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface PowerOffProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const PowerOff: React.FC<PowerOffProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = powerOffData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
