import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import MediaAudio_32x32_4 from '../../png/MediaAudio_32x32_4.png';
import MediaAudio_16x16_4 from '../../png/MediaAudio_16x16_4.png';

export const mediaAudioData = {
  '32x32_4': {
    imageSrc: MediaAudio_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: MediaAudio_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface MediaAudioProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const MediaAudio: React.FC<MediaAudioProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = mediaAudioData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
