import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Shortcut_32x32_4 from '../../png/Shortcut_32x32_4.png';
import Shortcut_16x16_4 from '../../png/Shortcut_16x16_4.png';

export const shortcutData = {
  '32x32_4': {
    imageSrc: Shortcut_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Shortcut_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface ShortcutProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Shortcut: React.FC<ShortcutProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = shortcutData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
