import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import CalcSc_32x32_4 from '../../png/CalcSc_32x32_4.png';
import CalcSc_16x16_4 from '../../png/CalcSc_16x16_4.png';

export const calcScData = {
  '32x32_4': {
    imageSrc: CalcSc_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: CalcSc_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface CalcScProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const CalcSc: React.FC<CalcScProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = calcScData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
