import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Mic_32x32_4 from '../../png/Mic_32x32_4.png';
import Mic_16x16_4 from '../../png/Mic_16x16_4.png';

export const micData = {
  '32x32_4': {
    imageSrc: Mic_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Mic_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface MicProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Mic: React.FC<MicProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = micData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
