import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import FolderExe_32x32_4 from '../../png/FolderExe_32x32_4.png';
import FolderExe_16x16_4 from '../../png/FolderExe_16x16_4.png';

export const folderExeData = {
  '32x32_4': {
    imageSrc: FolderExe_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: FolderExe_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface FolderExeProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const FolderExe: React.FC<FolderExeProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = folderExeData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
