import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Syncui124_32x32_4 from '../../png/Syncui124_32x32_4.png';

export const syncui124Data = {
  '32x32_4': {
    imageSrc: Syncui124_32x32_4 as string,
    width: 32,
    height: 32,
  },
};

export interface Syncui124Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4';
}

export const Syncui124: React.FC<Syncui124Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = syncui124Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
