import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Mkcompat900_32x32_1 from '../../png/Mkcompat900_32x32_1.png';
import Mkcompat900_32x32_4 from '../../png/Mkcompat900_32x32_4.png';

export const mkcompat900Data = {
  '32x32_1': {
    imageSrc: Mkcompat900_32x32_1 as string,
    width: 32,
    height: 32,
  },

  '32x32_4': {
    imageSrc: Mkcompat900_32x32_4 as string,
    width: 32,
    height: 32,
  },
};

export interface Mkcompat900Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_1' | '32x32_4';
}

export const Mkcompat900: React.FC<Mkcompat900Props> = ({
  variant = '32x32_1',
  ...rest
}) => {
  const image = mkcompat900Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
