import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Desk100_32x32_4 from '../../png/Desk100_32x32_4.png';
import Desk100_16x16_4 from '../../png/Desk100_16x16_4.png';

export const desk100Data = {
  '32x32_4': {
    imageSrc: Desk100_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Desk100_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Desk100Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Desk100: React.FC<Desk100Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = desk100Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
