import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Phone2_32x32_4 from '../../png/Phone2_32x32_4.png';
import Phone2_16x16_4 from '../../png/Phone2_16x16_4.png';

export const phone2Data = {
  '32x32_4': {
    imageSrc: Phone2_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Phone2_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Phone2Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Phone2: React.FC<Phone2Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = phone2Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
