import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Fontext3_32x32_4 from '../../png/Fontext3_32x32_4.png';
import Fontext3_16x16_4 from '../../png/Fontext3_16x16_4.png';

export const fontext3Data = {
  '32x32_4': {
    imageSrc: Fontext3_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Fontext3_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Fontext3Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Fontext3: React.FC<Fontext3Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = fontext3Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
