import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Quartz203_32x32_4 from '../../png/Quartz203_32x32_4.png';
import Quartz203_16x16_4 from '../../png/Quartz203_16x16_4.png';

export const quartz203Data = {
  '32x32_4': {
    imageSrc: Quartz203_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Quartz203_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Quartz203Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Quartz203: React.FC<Quartz203Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = quartz203Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
