import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Shdocvw258_32x32_4 from '../../png/Shdocvw258_32x32_4.png';
import Shdocvw258_16x16_4 from '../../png/Shdocvw258_16x16_4.png';

export const shdocvw258Data = {
  '32x32_4': {
    imageSrc: Shdocvw258_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Shdocvw258_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Shdocvw258Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Shdocvw258: React.FC<Shdocvw258Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = shdocvw258Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
