import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Sendmail2001_32x32_4 from '../../png/Sendmail2001_32x32_4.png';
import Sendmail2001_16x16_4 from '../../png/Sendmail2001_16x16_4.png';

export const sendmail2001Data = {
  '32x32_4': {
    imageSrc: Sendmail2001_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Sendmail2001_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Sendmail2001Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Sendmail2001: React.FC<Sendmail2001Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = sendmail2001Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
