import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Computer4_32x32_4 from '../../png/Computer4_32x32_4.png';
import Computer4_16x16_4 from '../../png/Computer4_16x16_4.png';

export const computer4Data = {
  '32x32_4': {
    imageSrc: Computer4_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Computer4_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Computer4Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Computer4: React.FC<Computer4Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = computer4Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
