import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Rnanp100_32x32_4 from '../../png/Rnanp100_32x32_4.png';
import Rnanp100_16x16_4 from '../../png/Rnanp100_16x16_4.png';

export const rnanp100Data = {
  '32x32_4': {
    imageSrc: Rnanp100_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Rnanp100_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Rnanp100Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Rnanp100: React.FC<Rnanp100Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = rnanp100Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
