import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import FileFind2_32x32_1 from '../../png/FileFind2_32x32_1.png';
import FileFind2_32x32_4 from '../../png/FileFind2_32x32_4.png';
import FileFind2_16x16_4 from '../../png/FileFind2_16x16_4.png';

export const fileFind2Data = {
  '32x32_1': {
    imageSrc: FileFind2_32x32_1 as string,
    width: 32,
    height: 32,
  },

  '32x32_4': {
    imageSrc: FileFind2_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: FileFind2_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface FileFind2Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_1' | '32x32_4' | '16x16_4';
}

export const FileFind2: React.FC<FileFind2Props> = ({
  variant = '32x32_1',
  ...rest
}) => {
  const image = fileFind2Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
