import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import User2_32x32_1 from '../../png/User2_32x32_1.png';
import User2_32x32_4 from '../../png/User2_32x32_4.png';

export const user2Data = {
  '32x32_1': {
    imageSrc: User2_32x32_1 as string,
    width: 32,
    height: 32,
  },

  '32x32_4': {
    imageSrc: User2_32x32_4 as string,
    width: 32,
    height: 32,
  },
};

export interface User2Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_1' | '32x32_4';
}

export const User2: React.FC<User2Props> = ({
  variant = '32x32_1',
  ...rest
}) => {
  const image = user2Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
