import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import User_32x32_4 from '../../png/User_32x32_4.png';
import User_16x16_4 from '../../png/User_16x16_4.png';

export const userData = {
  '32x32_4': {
    imageSrc: User_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: User_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface UserProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const User: React.FC<UserProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = userData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
