import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Rnaui103_32x32_4 from '../../png/Rnaui103_32x32_4.png';
import Rnaui103_16x16_4 from '../../png/Rnaui103_16x16_4.png';

export const rnaui103Data = {
  '32x32_4': {
    imageSrc: Rnaui103_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Rnaui103_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Rnaui103Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Rnaui103: React.FC<Rnaui103Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = rnaui103Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
