import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Icwdial102_32x32_4 from '../../png/Icwdial102_32x32_4.png';

export const icwdial102Data = {
  '32x32_4': {
    imageSrc: Icwdial102_32x32_4 as string,
    width: 32,
    height: 32,
  },
};

export interface Icwdial102Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4';
}

export const Icwdial102: React.FC<Icwdial102Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = icwdial102Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
