import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Cdplayer107_32x32_4 from '../../png/Cdplayer107_32x32_4.png';
import Cdplayer107_16x16_4 from '../../png/Cdplayer107_16x16_4.png';

export const cdplayer107Data = {
  '32x32_4': {
    imageSrc: Cdplayer107_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Cdplayer107_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Cdplayer107Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Cdplayer107: React.FC<Cdplayer107Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = cdplayer107Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
