import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import PrinterCalendar_32x32_4 from '../../png/PrinterCalendar_32x32_4.png';
import PrinterCalendar_16x16_4 from '../../png/PrinterCalendar_16x16_4.png';

export const printerCalendarData = {
  '32x32_4': {
    imageSrc: PrinterCalendar_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: PrinterCalendar_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface PrinterCalendarProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const PrinterCalendar: React.FC<PrinterCalendarProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = printerCalendarData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
