import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import CdMusic_32x32_4 from '../../png/CdMusic_32x32_4.png';
import CdMusic_16x16_4 from '../../png/CdMusic_16x16_4.png';

export const cdMusicData = {
  '32x32_4': {
    imageSrc: CdMusic_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: CdMusic_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface CdMusicProps extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const CdMusic: React.FC<CdMusicProps> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = cdMusicData[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
