import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Sndrec3215_32x32_4 from '../../png/Sndrec3215_32x32_4.png';
import Sndrec3215_16x16_4 from '../../png/Sndrec3215_16x16_4.png';

export const sndrec3215Data = {
  '32x32_4': {
    imageSrc: Sndrec3215_32x32_4 as string,
    width: 32,
    height: 32,
  },

  '16x16_4': {
    imageSrc: Sndrec3215_16x16_4 as string,
    width: 16,
    height: 16,
  },
};

export interface Sndrec3215Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4' | '16x16_4';
}

export const Sndrec3215: React.FC<Sndrec3215Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = sndrec3215Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
