import React from 'react';
import { IconProps } from '../iconType';
import { BaseIcon } from '../internal';
import Mmsys114_32x32_4 from '../../png/Mmsys114_32x32_4.png';

export const mmsys114Data = {
  '32x32_4': {
    imageSrc: Mmsys114_32x32_4 as string,
    width: 32,
    height: 32,
  },
};

export interface Mmsys114Props extends IconProps {
  /**
   * Icon variant to use.
   * also provides default styling with the correct height and width
   **/
  variant?: '32x32_4';
}

export const Mmsys114: React.FC<Mmsys114Props> = ({
  variant = '32x32_4',
  ...rest
}) => {
  const image = mmsys114Data[variant];

  return (
    <BaseIcon
      width={image.width}
      height={image.height}
      src={image.imageSrc}
      {...rest}
    />
  );
};
